<template>
    <div class="max-w-4xl mx-auto p-4">
      <h1 class="text-2xl font-bold mb-4">Weather Data</h1>
      <bar-chart :chart-data="chartData" />
    </div>
  </template>
  
  <script>
  import { ref, onMounted } from 'vue';
  import axios from 'axios';
  import BarChart from './BarChart.vue';
  
  export default {
    components: {
      BarChart
    },
    setup() {
      const chartData = ref(null);
  
      const fetchWeatherData = async () => {
        try {
          const response = await axios.get('/api/weather');
          const data = response.data;
          console.log('API Data:', data);
  
          const uniqueData = data.reduce((acc, current) => {
            const x = acc.find(item => item.base_time === current.base_time);
            if (!x) {
              return acc.concat([current]);
            } else {
              return acc;
            }
          }, []);
  
          chartData.value = {
            labels: uniqueData.map(item => item.base_time),
            datasets: [
              {
                label: 'Temperature (°C)',
                backgroundColor: '#FF6384',
                data: uniqueData.map(item => item.temperature),
              },
              {
                label: 'Humidity (%)',
                backgroundColor: '#36A2EB',
                data: uniqueData.map(item => item.humidity),
              },
            ],
          };
        } catch (error) {
          console.error('API 호출 오류:', error);
        }
      };
  
      onMounted(fetchWeatherData);
  
      return { chartData };
    }
  };
  </script>
  
